.user-details {
  border: 1px solid black;
  background-color: rgb(173, 173, 173);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: Arial, sans-serif;
  padding: 20px;

  .coins-details {
    margin-inline-start: -151px;
  }

  .coins-details,
  .bitcoin-details {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
  }

  .coins-details h1,
  .bitcoin-details h1 {
    font-size: 24px;
    margin-left: 10px;
  }

  img {
    width: 50px;
    height: 50px;
    object-fit: contain;
    margin-right: 10px;
  }
}
